import React, { useEffect, useState } from 'react';
import './Login.css';
import { toast } from 'react-toastify';

function LoginForm() {
  const [username, setUsername] = useState('');
  const [userId, setUserId] = useState('');
  const [password, setPassword] = useState('');


  const handleSubmit = async (e) => {
    e.preventDefault();
    const endpoint = "https://script.google.com/macros/s/AKfycbzdNA0pW96YPpcHnUKH4VhsCi8Gcm8TR-JYrw7fVajXYaZX_Sbr4qgn74gryfktpRs/exec";

    try {
      const formData = new URLSearchParams();
      const id = 6182;
      formData.append('Action', "login");
      formData.append('UserId', id);
      formData.append('Name', username);
      formData.append('Password', password);

      console.log('Form data:', formData.toString());
      const response = await fetch(endpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
        body: formData.toString(), // Convert to string
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();

      if (data.success) {
        toast.success(data.message || 'Login successful!');
        console.log('Login successful:', data);
        setUsername('');
        setPassword('');
      } else {
        toast.error(data.message || 'Login failed. Please try again.');
        console.error('Login failed:', data);
      }
    } catch (error) {
      toast.error(error.message || 'An error occurred. Please try again.');
      console.error('Error:', error);
    }
  };

  return (
    <div className="login-container">
      <h2>Login</h2>
      <form className="login-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <input
            type="text"
            id="username"
            name="username"
            placeholder="Username or Email Address *"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
        </div>
        <div className="form-group">
          <input
            type="password"
            id="password"
            name="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <div className="form-actions">
          <div className="remember-me">
            <input type="checkbox" id="remember" name="remember" />
            <label htmlFor="remember">Remember me</label>
          </div>
          <a href="/forget-password" className="forgot-password">Forgot your password?</a>
        </div>
        <br />
        <hr />
        <div className="button-group">
          <button type="submit" className="login-button">Submit</button>
          <a href='#/registration' className="anchor">Register</a>
        </div>
      </form>
    </div>
  );
}

export default LoginForm;
